// ---------------------------------------------------------===
// culture page styles
// ---------------------------------------------------------===
.culture-section {
    margin: 0 0 128px;
    @include media('<tablet') {
        background: $light-blue;
        margin: 0 0 72px;
    }
    .intro {
        margin: 0 0 23px;
        h4 {
            margin: 0;
        }
        .col-links {
            @include media('<tablet') {
                margin-bottom: 29px;
            }
        }
    }
    .full-image {
        margin-top: 78px;
        margin-bottom: 115px;
        @include media('<tablet') {
            margin: 0 0 62px;
        }
    }
}

.section-culture {
    padding: 240px 0 98px;
    background: $light-blue;
    @include media('<desktop') {
        padding: 120px 0 73px;
    }
    @include media('<tablet') {
        padding: 70px 0 60px;
    }
    .intro {
        margin-bottom: 220px;
        @include media('<desktop') {
            margin-bottom: 80px;
        }
        @include media('<tablet') {
            margin-bottom: 94px;
        }
        h3 {
            margin-bottom: 40px;
            @include media('<tablet') {
                margin-bottom: 26px;
            }
        }
        p {
            @include media('<tablet') {
                margin-bottom: 23px;
            }
        }
    }
    .collage {
        @extend %listreset;
        overflow: hidden;
        margin: 0 -10px;
        @include media('<tablet') {
            margin: 0 -20px;
        }
        li {
            float: left;
            padding: 0 10px 20px;
            @include media('<tablet') {
                padding: 2px 4px;
            }
            &:nth-child(1) {
                width: 61.12%;
            }
            &:nth-child(2) {
                width: 38.88%;
            }
            &:nth-child(3) {
                width: 36%;
                clear: left;
            }
            &:nth-child(4) {
                width: 25%;
            }
            &:nth-child(5) {
                width: 25.6%;
            }
            &:nth-child(6) {
                width: 13.4%;
            }
            &:nth-child(7) {
                width: 36%;
                clear: left
            }
            &:nth-child(8) {
                width: 25%;
                margin-top: -3.5%;
            }
            &:nth-child(9) {
                width: 39%;
                margin-top: -14.3%;
            }
            &:nth-child(10) {
                width: 18.8%;
            }
            &:nth-child(11) {
                width: 20.2%;
            }
        }
    }
}

.image-collage {
    .align-bottom {
        align-items: flex-end;
    }
    .img1 {
        margin-bottom: -25px;
        position: relative;
        z-index: 3;
        @include media('<tablet') {
            margin-top: 22%;
            margin-bottom: -5px;
        }
    }
    .img2 {
        margin-bottom: -68px;
        @include media('<tablet') {
            margin-bottom: 45px;
        }
    }
    .img3 {
        margin-bottom: 73px;
    }
    .img4 {
        margin-top: -14px;
        @include media('<tablet') {
            margin-top: 0;
            margin-bottom: -10px;
        }
    }
    .img5 {
        margin-top: 88px;
        @include media('<tablet') {
            margin-top: -59px;
        }
    }
    .img6 {
        margin-top: -272px;
        @include media('<desktop') {
            margin-top: -150px;
        }
        @include media('<tablet') {
            margin-top: 0;
        }
    }
    .img7 {
        margin-bottom: -30px;
        z-index: 1;
        @include media('<tablet') {
            margin-bottom: 0;
        }
    }
    .img8 {
        @include media('<tablet') {
            order: -1;
            margin-bottom: 16px;
        }
    }
    .img9 {
        @include media('<tablet') {
            margin-top: -40px;
            position: relative;
            z-index: 1;
        }
        .offset-sm {
            //margin-right: -50px;
            margin-left: -15px;
        }
    }
    .img10 {
        margin-top: 149px;
        @include media('<tablet') {
            margin-top: 50px;
        }
    }
}

.testimonial-row {
    @include media('<tablet') {
        order: -2;
    }
}

.testimonial {
    display: flex;
    margin-bottom: 40px;
    .img-hold {
        width: 200px;
        @include media('<tablet') {
            width: 108px;
        }
    }
    blockquote {
        flex: 1;
        margin: 0;
        q {
            font-size: 40px;
            line-height: 1.35;
            display: block;
            margin: 0 0 15px;
            @include media('<desktop') {
                font-size: 35px;
                @include media('<tablet') {
                    font-size: 24px;
                    line-height: 1.167;
                }
            }
            &:before,
            &:after {
                display: none;
            }
        }
        cite {
            font-style: normal;
            @include media('<tablet') {
                font-size: 12px;
            }
        }
    }
}

.team-section {
    .intro {
        margin: 0 0 53px;
        width: 50%;
        @include media('<tablet') {
            margin: 0 0 27px;
            width: 100%;
        }
        h2 {
            font-size: 72px;
            @include media('<tablet') {
                margin: 0 0 18px;
            }
            span {
                display: block;
                @include media('<tablet') {
                    display: inline-block;
                }
            }
        }
        h3 {
            line-height: 1;
            font-weight: 400;
        }
    }
}

.bio-section {
    padding-bottom: 40px;
    @include media('<tablet') {
        padding-bottom: 20px;
    }
    .heading-row {
        align-items: flex-end;
        margin-bottom: 17px;
        .heading {
            h2 {
                font-size: 48px;
                margin: 0 0 4px;
                @include media('<tablet') {
                    font-size: 36px;
                    margin: 0 0 1px;
                }
            }
            p {
                margin: 0;
            }
        }
        .back-link {
            @include media('<tablet') {
                margin: 0 0 14px;
            }
        }
    }
    .avatar-col {
        padding-top: 11px;
        @include media('<tablet') {
            padding-top: 0;
            margin-bottom: 27px;
        }
        .avatar-wrap {
            margin: 0 0 37px;
        }
        h4 {
            margin: 0 0 12px;
            font-size: 14px;
            line-height: 1.8;
            font-weight: 900;
        }
    }
    .link-list {
        @extend %listreset;
        @include media('<tablet') {
            column-count: 2;
            max-width: 360px;
        }
        li {
            +li {
                margin-top: 8px;
            }
            a {
                font-size: 20px;
            }
        }
    }
    .desc-col {
        p {
            margin: 0 0 36px;
            @include media('<tablet') {
                margin: 0 0 28px;
            }
        }
    }
}

.culture--a {
    padding: 149px 0 66px;
    @include media('<desktop') {
        padding-top: 120px;
    }
    @include media('<tablet') {
        padding: 113px 0 0;
    }
    h2 {
        max-width: 399px;
    }
    h4 {
        @include media('<tablet') {
            font-size: 24px;
            margin-bottom: 16px;
        }
    }
    .intro {
        margin-bottom: 127px;
        @include media('<desktop') {
            margin-bottom: 100px;
        }
        @include media('<tablet') {
            margin-bottom: 82px;
        }
    }
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-y: auto;
    background: rgba($black, .76);
    padding: 0 10px;
    z-index: 99;
    &-active {
        overflow: hidden;
    }
    &-inner {
        max-width: 1080px;
        margin: 40px auto;
        min-height: calc(100% - 80px);
        display: flex;
        align-items: center;
        @include media('<tablet') {
            min-height: calc(100% - 40px);
            margin: 20px auto;
        }
    }
    &-box {
        background: $white;
        padding: 55px 20px 33px;
        line-height: 1.51;
        position: relative;
        @include media('>=tablet') {
            padding: 79px 40px 65px;
            display: flex;
        }
        @include media('>=desktop') {
            padding: 79px 10% 65px;
        }
        .close {
            position: absolute;
            top: 21px;
            right: 20px;
            font-size: 22px;
            text-decoration: none;
            &:hover {
                color: $blue;
            }
        }
        .left {
            @include media('>=tablet') {
                width: 36%;
                margin-right: 7.4%;
            }
            @include media('<tablet') {
                margin-bottom: 26px;
            }
        }
        .right {
            @include media('>=tablet') {
                flex: 1;
            }
        }
        .head {
            margin-bottom: 41px;
            @include media('<tablet') {
                margin-bottom: 28px;
            }
        }
        h2 {
            margin-bottom: 14px;
        }
        h5 {
            text-transform: uppercase;
            letter-spacing: 1px;
            margin-bottom: 0;
        }
        p {
            margin-bottom: 0;
            +p {
                margin-top: 30px;
            }
        }
    }
}

.preload-image {
    position: absolute;
    width: 0;
    height: 0;
    overflow: hidden;
    z-index: -1;
    opacity: 0;
}

.banner-office {
    min-height: 564px;
    display: flex;
    align-items: center;
    @include media('<tablet') {
        min-height: 320px;
        margin-top: 100px;
    }
    .container {
        width: 100%;
    }
    &-links {
        @extend %listreset;
        font: 500 96px/1 $recife;
        text-transform: capitalize;
        @include media('<desktop') {
            font-size: 48px;
        }
        @include media('<tablet') {
            font-size: 38px;
        }
        li {
            margin: 11px 0;
        }
        a:hover,
        .active a {
            color: $black;
        }
        a {
            color: rgba($black, .3);
            text-decoration: none;
            @include animate(color);
        }
    }
}

.back-link {
    a {
        .icon-left-arrow {
            margin-right: 16px;
            font-size: 13px;
        }
        text-decoration: none !important;
    }
}