  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
}

.wrapper-protected__casestudy .wrapper-protected__form {
  width: 100%;
  max-width: 400px;
}

.wrapper-protected__casestudy .post-password-form {
  text-align: center;
}

.wrapper-protected__casestudy .post-password-form p {
  font-size: 1.125rem;
}

.wrapper-protected__casestudy .post-password-form label {
  display: block;
  width: 100%;
  color: #A3A3A6;
}

.wrapper-protected__casestudy .post-password-form input[type=password] {
  display: block;
  border-width: 0 0 1px 0;
  width: 100%;
  color: black;
  font-size: 1.25rem;
}

.wrapper-protected__casestudy .post-password-form input[type=submit] {
  margin-top: 47px;
  display: block;
  background-color: black;
  color: white;
  width: 100%;
  height: 70px;
  text-align: center;
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 0.05em;
}

.wrapper-protected__casestudy .post-password-form input[type=submit]:hover {
  background-color: #333333;
}

